@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

select.box{-webkit-appearance:none;-moz-appearance:none;appearance:none;width:3rem;border:0;cursor:pointer;border-right:2px solid #fff}select.box:focus{outline:none}.a{background-color:#5FE632}.b{background-color:#527DAD}.hi{background-color:#FDA428}.v{background-color:#ab10b1}.ua{background-color:#FC0D1B}

.selectBoxWrap{display:flex}.mentorBlock h4{color:#3DC0EB;margin-left:1rem}.mentorBlock .leftBlock{display:flex;align-items:center}.mentorBlock .name{color:#3DC0EB;vertical-align:middle}.mentorBlock .name span{display:inline-block;vertical-align:middle}.mentorBlock .phone{color:#7d7b7b;vertical-align:top}.mentorBlock .phone svg{width:1.25rem;margin-right:0.25rem;display:inline-block;vertical-align:middle}.mentorBlock .phone span{vertical-align:middle;display:inline-block}.mentorBlock .star{margin-left:0.25rem}.mentorBlock .star svg{width:1.5rem;margin:0 0.15rem}.mentorBlock .nameBlock{width:50%;overflow:hidden}.mentorBlock .checkBox{display:flex;align-items:center}.mentorBlock .checkBox label{cursor:pointer}.mentorBlock .tick{position:relative;background:none;padding-left:0.5rem;vertical-align:middle}.mentorBlock .tick:before{content:'';width:1.5rem;height:1.5rem;background:#fff;display:inline-block;position:absolute;left:-1.5rem;border:1px solid #ddd;top:-0.3rem}.mentorBlock .tick:after{content:'';width:1rem;height:1.75rem;background:transparent;display:inline-block;position:absolute;left:-1.25rem;pointer-events:none;transition:0.15s;opacity:0;border:solid #fff;border-width:0 0.2rem 0.2rem 0;transform:scale(1) rotate(45deg);top:-0.55rem}.mentorBlock .check{width:1.5rem;opacity:0}.mentorBlock .check:checked+label .tick:before{background-color:#4399E8}.mentorBlock .check:checked+label .tick:after{opacity:1;transform:scale(0.5) rotate(45deg)}.mentorBlock .note{text-align:right;width:31%}.mentorBlock .note>p{overflow:hidden;text-transform:capitalize;text-overflow:ellipsis;padding-left:1rem;text-align:right}.mentorBlock .note button{background:none;border:0;font-size:1rem;color:#3DC0EB;display:inline-block;cursor:pointer}.mentorBlock .note button:focus{outline:none}.mentorBlock .note input[type=text]{display:inline-block;width:60%;border-radius:2px;border:1px solid #ddd;padding:0.25rem}.mentorBlock .note input[type=submit]{display:inline-block;background-color:#02da05;border:1px solid #fff;color:#fff;padding:0.25rem;border-radius:4px}.mentorBlock .note input[type=submit]:focus{outline:none}

.filterBlock{justify-content:space-between;align-items:center;padding:1rem}.filterBlock ul{list-style-type:none}.filterBlock li{display:inline-block;vertical-align:middle;margin:0 0.5rem;position:relative}.filterBlock label{font-size:1.17rem;display:inline-block;text-transform:capitalize;cursor:pointer}.filterBlock .tick{position:relative;background:none}.filterBlock .tick.a:before{background-color:#5FE632}.filterBlock .tick.b:before{background-color:#527DAD}.filterBlock .tick.hi:before{background-color:#FDA428}.filterBlock .tick.v:before{background-color:#ab10b1}.filterBlock .tick.ua:before{background-color:#FC0D1B}.filterBlock .tick:before{content:'';width:1.2rem;height:1.2rem;background:#ccc;display:inline-block;position:absolute;left:-1.5rem}.filterBlock .tick:after{content:'';width:1.2rem;height:1.2rem;background:#1b1b1b;display:inline-block;position:absolute;left:-1.5rem;pointer-events:none;transition:0.15s;opacity:0;transform:scale(1)}.filterBlock input{display:inline-block}.filterBlock input[type=search]{padding:0.5rem 1rem 0.5rem 1rem;border:1px solid #C0C0C0;border-radius:3px}.filterBlock input[type=search]:focus{outline:none;border-color:#3DC0EB}.filterBlock input[type=radio]{background-color:red;width:2rem;visibility:hidden}.filterBlock input[type=radio]:checked+label .tick:after{opacity:1;transform:scale(0.5)}

*{box-sizing:border-box;margin:0;padding:0}html{font-size:12px}body{background-color:#F3F5F7;font-family:'Jost', sans-serif}.container{background-color:#fff;margin:30px auto;width:1000px;overflow:hidden;overflow-x:auto}.box{width:3rem;height:3rem;overflow:hidden;text-align-last:center}.box span{display:block}.dateWrap{display:flex;font-size:0.8rem}.container>section{display:flex;position:relative}.title{position:absolute;top:-20px}.leftBlock,.rightBlock{width:50%}.mentorList{width:100%;border-top:1px solid #F3F5F7}.mentorList article{display:flex;border-bottom:2px solid #F3F5F7;padding:0.5rem;padding-left:1rem}

